import React, {  useEffect } from "react";
import {  useSelector, useDispatch } from "react-redux";
import $ from "jquery";
import {Rating} from '@material-ui/lab'
import { addCartItem, removeCartItem } from "../action/userAction";

export const DetailHeading = (props) => {
    const course = props.course;
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const dispatch = useDispatch();
    const addCartHandler = (courseId) => {
        dispatch(addCartItem(courseId));
    };
    const removeCartItemHandler = (id) => {
        dispatch(removeCartItem(id));
    };
    useEffect(() => {
        $(".heart").hover(function () {
            $("#hearts").removeClass("far fa-heart heartss");
            $("#hearts").addClass("fas fa-heart hearted");
        });
        $(".heart").mouseout(function () {
            $("#hearts").removeClass("fas fa-heart heartss");
            $("#hearts").addClass("far fa-heart hearted");
        });
        return () => {
            $(".heart").unbind();
        };
    }, []);
    useEffect(() => {
        $(window).scroll(function () {
            if ($(window).scrollTop() >= $("div.fullwitdh").height() + 100) {
                $("#right").css("position", "fixed");
                $("#right").css("margin-top", "100px");
                $("img.course-image").hide("slow", function () {});
            } else {
                $("#right").css("position", "absolute");
                $("#right").css("margin-top", "0px");
                $("img.course-image").show("slow", function () {});
            }
        });
        return () => {
            $(window).unbind();
        };
    }, []);
    return (
        <div className="row">
            <div className="col-8">
                <div className="left">
                    <h1 className="name-course">{course.title}</h1>
                    <div className="note"></div>
                    <div className="left-row">
                        <Rating value={parseInt(course.rating)} readOnly/>    
                        <div className="rating-details">
                            <span>{course.rating} (1,111 đánh giá)</span>
                        </div>
                        <div className="student-enroll">
                            <span>Có {course.numOfStudent} học sinh đã đăng ký</span>
                        </div>
                    </div>
                    <div className="left-row">
                        <div className="author">Giảng Viên: {course.lecture}</div>
                    </div>
                    <div className="last-update" style={{ marginTop: 25 }}>
                        <span>Sửa đổi lần cuối: 17/06/2020</span>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div
                    className="jsright right-col-content"
                    id="right"
                    style={{ width: 354, left: "auto", top: 10, position: "absolute" }}
                >
                    <div className="right-module">
                        <div className="image-course">
                            <img
                                className="course-image"
                                src={course.pic}
                                alt=""
                                style={{ width: "100%", height: 140 }}
                            />
                        </div>
                        <div className="right-content">
                            <div className="Detail-floating">
                                <div className="buy">
                                    <div className="price">
                                        <div className="now-price">
                                            <span className="course-price-now">{course.price} VND</span>
                                        </div>
                                        <div className="non-purchase">
                                            <span className="heart">
                                                <i id="hearts" className="far fa-heart heartss"></i>
                                            </span>
                                        </div>
                                    </div>
                                    <div className="confirm-buy">
                                        {userInfo ? (
                                            userInfo.cart.includes(course._id) ? (
                                                <div className="button-add-to-cart" style={{ marginTop: 10 }}>
                                                    <button
                                                        type="button"
                                                        className="btn btn-danger"
                                                        style={{ width: "100%", height: 50, backgroundColor: "#4CAF50" }}
                                                        onClick={() => {
                                                            removeCartItemHandler(course._id);
                                                        }}
                                                    >
                                                        Bỏ ra khỏi giỏ hàng
                                                    </button>
                                                </div>
                                            ) : (
                                                <div className="button-add-to-cart" style={{ marginTop: 10 }}>
                                                    <button
                                                        type="button"
                                                        className="btn btn-danger"
                                                        style={{ width: "100%", height: 50 }}
                                                        onClick={() => {
                                                            addCartHandler(course._id);
                                                        }}
                                                    >
                                                        Thêm vào giỏ hàng
                                                    </button>
                                                </div>
                                            )
                                        ) : (
                                            <div className="button-buy-now" style={{ marginTop: 10 }}>
                                                <button
                                                    type="button"
                                                    className="btn btn-primary"
                                                    style={{ width: "100%", height: 50 }}
                                                >
                                                    Đăng nhập để thêm vào giỏ hàng
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                </div>
                                <div className="includes">
                                    <ul className="list">
                                        <li>
                                            <i className="far fa-clock"></i>
                                            <span>20h học</span>
                                        </li>
                                        <li className="list-item">
                                            <i className="far fa-file"></i>
                                            <span>150 bài viết</span>
                                        </li>
                                        <li className="list-item">
                                            <i className="fas fa-heartbeat"></i>
                                            <span>Truy cập trọn đời</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
